













export default {
	data() {
		return {
			userDTO: {
				mobile: '',
				password: ''
			}
		};
	},
	onLoad() {},
	onLoad: function(option) {
		//option为object类型，会序列化上个页面传递的参数
		console.log(option.mobile);
		this.mobile = option.mobile;
	},
	methods: {
		signUp: function(userDTO) {
			var _this = this;
			uni.request({
				url: this.apiServer + '/user/sign_up',
				method: 'POST',
				header: { 'content-type': 'application/json' },
				data: {
					mobile: _this.mobile,
					password: _this.password
				},
				success: res => {
					if (res.data.code === 0) {
						uni.showModal({
							title: '提示',
							content: '注册成功',
							confirmText:'立即登录',
							cancelText:'稍后登录',
							success: (login) => {
								if(login.confirm){
									uni.navigateTo({
										url:'../signin/signin'
									})
								}else if(login.cancel){
									uni.switchTab({
										url:'../my/my'
									})
								}
							}
						});
					} else {
						uni.showModal({
							title: '提示',
							content: res.data.msg
						});
					}
				}
			});
		}
	}
};
